<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-calendar"></i> 课程设计
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container" v-loading="loading">
            <div class="form"> 
                <div class="form-top">
                    <el-radio-group size="small" @change="changetab" v-model="choosed">
                      <el-radio-button label="未完成课程设计教学"></el-radio-button>
                      <el-radio-button label="已完成课程设计教学"></el-radio-button>
                    </el-radio-group>
                    <el-button class="fr" type="primary" @click="openLink(-1)" size="small">新建课程设计教学</el-button>
                </div>
                <div class="cont">
                    <div class="cont-one" v-for="item in items">
                        <div class="one-left">
                            <h2>{{item.name}}</h2>
                            <p class="stu-num"><strong>参加学生：</strong>{{item.studentCount}}人</p>
                            <p class="work-time"><strong>作业提交：</strong>{{item.deadlineTime}} 之前</p>
                            <p class="status"><strong>当前状态：</strong>{{item.statusDescribe}}</p>
                        </div>
                        <div class="one-right">
                            <p><el-button type="primary" size="mini" @click="inLink(item.id)" plain>进入教学</el-button></p>
                            <p><el-button type="primary" size="mini" @click="openLink(item.id)" plain>修改教学</el-button></p>
                        </div>
                    </div>

                    <div class="pagination">
                          <el-pagination
                            background
                            :page-size="pageSize"
                            @current-change="changePage"
                            layout="prev, pager, next"
                            :total="total">
                          </el-pagination>
                      </div>
                    
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {fetchGetteachingbycreatoridandstatus} from "../../axios/requests";
export default {
    name: 'course', 
    data() {
        return {
            choosed:'未完成课程设计教学',
            items:[],

            loading:false,
            pageNum:1,
            pageSize:10,
            total:0,
        };
    },
    mounted(){
        this.Getteachingbycreatoridandstatus()
    },
    methods: {
        openLink(pid){
            this.$openBlank('newcourse',{pid});
        },
        inLink(pid){
            this.$openBlank('stuworklist',{pid});
        },
        changetab(e){
            this.pageNum = 1;
            this.Getteachingbycreatoridandstatus();
        },
        changePage(page){
          this.pageNum = page;
          this.Getteachingbycreatoridandstatus();
        },
        async Getteachingbycreatoridandstatus() {//得到教学列表
          this.loading = true;
          let {pageNum, pageSize, choosed} = this;
          var status = choosed=="未完成课程设计教学"?0:1;
          try {
            let response = await fetchGetteachingbycreatoridandstatus(status, pageNum, pageSize);
            if (response.status.code ==0) {
              this.total = response.result.total;
              this.items = response.result.list;
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            this.loading = false;
          }
        },
    }
};
</script>
<style lang='scss' scoped>
    .cont-one{
        display:flex;
        padding:15px 20px;
        border:1px solid #ccc;
        margin-top:20px;
        .one-left{
            flex:1;
            font-size:14px;
            line-height:24px;
            color:#666;
            strong{
                font-weight:500;
                color:#333;
            }
            h2{
                font-size:16px;
                line-height:28px;
            }
        }
        .one-right{
            width:90px;
            padding-top:20px;
            p{
                margin-top:10px;
            }
            button{
                text-align: right;
                display:block;
                margin-left:10px;
            }
        }
    }
    .pagination{
        margin-top:20px;
    }
</style>


